/**
 * Patient Validation Service
 * @fileoverview Handles patient data validation and business rules
 * @version 1.0.0
 */

import { Patient } from '../../types/patient';

export interface ValidationResult {
  isValid: boolean;
  errors: ValidationError[];
  warnings: ValidationWarning[];
}

export interface ValidationError {
  field: string;
  code: string;
  message: string;
  severity: 'error' | 'warning';
}

export interface ValidationWarning {
  field: string;
  code: string;
  message: string;
  suggestion?: string;
}

export class PatientValidationService {
  
  validatePatient(patient: Partial<Patient>, existingPatients: Patient[] = []): ValidationResult {
    const errors: ValidationError[] = [];
    const warnings: ValidationWarning[] = [];

    // Required field validations
    this.validateRequiredFields(patient, errors);
    
    // Format validations
    this.validateFormats(patient, errors, warnings);
    
    // Business rule validations
    this.validateBusinessRules(patient, errors, warnings);
    
    // Uniqueness validations
    this.validateUniqueness(patient, existingPatients, errors);

    return {
      isValid: errors.filter(e => e.severity === 'error').length === 0,
      errors,
      warnings
    };
  }

  validateTcNumber(tcNumber: string, excludePatientId?: string, existingPatients: Patient[] = []): boolean {
    // Basic format check
    if (!tcNumber || tcNumber.length !== 11) {
      return false;
    }

    // Must be all digits
    if (!/^\d{11}$/.test(tcNumber)) {
      return false;
    }

    // TC Number algorithm validation
    if (!this.isValidTcNumberAlgorithm(tcNumber)) {
      return false;
    }

    // Check uniqueness
    const existingPatient = existingPatients.find(p => 
      p.tcNumber === tcNumber && p.id !== excludePatientId
    );
    
    return !existingPatient;
  }

  validatePhone(phone: string): boolean {
    if (!phone) return false;
    
    // Remove all non-digit characters
    const cleanPhone = phone.replace(/\D/g, '');
    
    // Turkish phone number validation
    // Mobile: 5XX XXX XX XX (10 digits starting with 5)
    // Landline: 2XX XXX XX XX or 3XX XXX XX XX (10 digits starting with 2 or 3)
    // With country code: +90 or 0090 prefix
    
    if (cleanPhone.length === 10) {
      // Local format: 5XXXXXXXXX or 2XXXXXXXXX or 3XXXXXXXXX
      return /^[235]\d{9}$/.test(cleanPhone);
    } else if (cleanPhone.length === 11) {
      // With leading 0: 05XXXXXXXXX or 02XXXXXXXXX or 03XXXXXXXXX
      return /^0[235]\d{9}$/.test(cleanPhone);
    } else if (cleanPhone.length === 12) {
      // With country code: 905XXXXXXXXX or 902XXXXXXXXX or 903XXXXXXXXX
      return /^90[235]\d{9}$/.test(cleanPhone);
    } else if (cleanPhone.length === 13) {
      // With 00 prefix: 0090XXXXXXXXXX
      return /^0090[235]\d{9}$/.test(cleanPhone);
    }
    
    return false;
  }

  validateEmail(email: string): boolean {
    if (!email) return true; // Email is optional
    
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  }

  validateBirthDate(birthDate: string): boolean {
    if (!birthDate) return true; // Birth date is optional
    
    const date = new Date(birthDate);
    const now = new Date();
    
    // Must be a valid date
    if (isNaN(date.getTime())) {
      return false;
    }
    
    // Must be in the past
    if (date >= now) {
      return false;
    }
    
    // Must be reasonable (not more than 120 years ago)
    const maxAge = new Date();
    maxAge.setFullYear(maxAge.getFullYear() - 120);
    
    return date >= maxAge;
  }

  private validateRequiredFields(patient: Partial<Patient>, errors: ValidationError[]): void {
    // Phone is required
    if (!patient.phone || !patient.phone.trim()) {
      errors.push({
        field: 'phone',
        code: 'REQUIRED',
        message: 'Telefon numaras zorunludur',
        severity: 'error'
      });
    }

    // Name is required (either combined name or firstName + lastName)
    if (!patient.name && (!patient.firstName || !patient.lastName)) {
      errors.push({
        field: 'name',
        code: 'REQUIRED',
        message: 'Hasta ad zorunludur',
        severity: 'error'
      });
    }
  }

  private validateFormats(patient: Partial<Patient>, errors: ValidationError[], warnings: ValidationWarning[]): void {
    // Phone format validation
    if (patient.phone && !this.validatePhone(patient.phone)) {
      errors.push({
        field: 'phone',
        code: 'INVALID_FORMAT',
        message: 'Geersiz telefon numaras format',
        severity: 'error'
      });
    }

    // TC Number format validation
    if (patient.tcNumber && !this.isValidTcNumberAlgorithm(patient.tcNumber)) {
      errors.push({
        field: 'tcNumber',
        code: 'INVALID_FORMAT',
        message: 'Geersiz TC Kimlik Numaras',
        severity: 'error'
      });
    }

    // Email format validation
    if (patient.email && !this.validateEmail(patient.email)) {
      errors.push({
        field: 'email',
        code: 'INVALID_FORMAT',
        message: 'Geersiz e-posta adresi format',
        severity: 'error'
      });
    }

    // Birth date validation
    if (patient.birthDate && !this.validateBirthDate(patient.birthDate)) {
      errors.push({
        field: 'birthDate',
        code: 'INVALID_DATE',
        message: 'Geersiz doum tarihi',
        severity: 'error'
      });
    }
  }

  private validateBusinessRules(patient: Partial<Patient>, errors: ValidationError[], warnings: ValidationWarning[]): void {
    // Age-related warnings
    if (patient.birthDate) {
      const age = this.calculateAge(patient.birthDate);
      
      if (age < 18) {
        warnings.push({
          field: 'birthDate',
          code: 'MINOR_PATIENT',
          message: 'Hasta 18 yandan kk',
          suggestion: 'Veli bilgilerini eklemeyi unutmayn'
        });
      }
      
      if (age > 90) {
        warnings.push({
          field: 'birthDate',
          code: 'ELDERLY_PATIENT',
          message: 'Hasta 90 yandan byk',
          suggestion: 'Ya bilgisini kontrol edin'
        });
      }
    }

    // Status consistency checks
    if (patient.status === 'archived' && patient.segment !== 'renewal') {
      warnings.push({
        field: 'status',
        code: 'STATUS_SEGMENT_MISMATCH',
        message: 'Arivlenmi hasta iin segment uyumsuzluu',
        suggestion: 'Segment bilgisini kontrol edin'
      });
    }

    // Device-related validations
    if (patient.deviceTrial && !patient.trialDate) {
      warnings.push({
        field: 'trialDate',
        code: 'MISSING_TRIAL_DATE',
        message: 'Deneme cihaz var ama deneme tarihi eksik',
        suggestion: 'Deneme tarihini ekleyin'
      });
    }

    if (patient.purchased && !patient.purchaseDate) {
      warnings.push({
        field: 'purchaseDate',
        code: 'MISSING_PURCHASE_DATE',
        message: 'Satn alm var ama satn alma tarihi eksik',
        suggestion: 'Satn alma tarihini ekleyin'
      });
    }
  }

  private validateUniqueness(patient: Partial<Patient>, existingPatients: Patient[], errors: ValidationError[]): void {
    // TC Number uniqueness
    if (patient.tcNumber) {
      const duplicate = existingPatients.find(p => 
        p.tcNumber === patient.tcNumber && p.id !== patient.id
      );
      
      if (duplicate) {
        errors.push({
          field: 'tcNumber',
          code: 'DUPLICATE',
          message: `Bu TC Kimlik Numaras baka bir hasta tarafndan kullanlyor: ${duplicate.name}`,
          severity: 'error'
        });
      }
    }

    // Phone uniqueness (warning only, as family members might share phones)
    if (patient.phone) {
      const duplicate = existingPatients.find(p => 
        p.phone === patient.phone && p.id !== patient.id
      );
      
      if (duplicate) {
        errors.push({
          field: 'phone',
          code: 'DUPLICATE_WARNING',
          message: `Bu telefon numaras baka bir hasta tarafndan kullanlyor: ${duplicate.name}`,
          severity: 'warning'
        });
      }
    }
  }

  private isValidTcNumberAlgorithm(tcNumber: string): boolean {
    if (!tcNumber || tcNumber.length !== 11) return false;
    
    const digits = tcNumber.split('').map(Number);
    
    // First digit cannot be 0
    if (digits[0] === 0) return false;
    
    // Calculate check digits
    const oddSum = digits[0] + digits[2] + digits[4] + digits[6] + digits[8];
    const evenSum = digits[1] + digits[3] + digits[5] + digits[7];
    
    const check1 = ((oddSum * 7) - evenSum) % 10;
    const check2 = (oddSum + evenSum + digits[9]) % 10;
    
    return digits[9] === check1 && digits[10] === check2;
  }

  private calculateAge(birthDate: string): number {
    const birth = new Date(birthDate);
    const today = new Date();
    
    let age = today.getFullYear() - birth.getFullYear();
    const monthDiff = today.getMonth() - birth.getMonth();
    
    if (monthDiff < 0 || (monthDiff === 0 && today.getDate() < birth.getDate())) {
      age--;
    }
    
    return age;
  }
}

export const patientValidationService = new PatientValidationService();