import React from 'react';
import { Filter, X } from 'lucide-react';
import { usePatientFilters } from '@/hooks/patients/usePatientFilters';

interface PatientFiltersProps {
  className?: string;
}

export const PatientFilters: React.FC<PatientFiltersProps> = ({
  className = ""
}) => {
  const { filters, updateFilter, clearAllFilters } = usePatientFilters();

  const statusOptions = [
    { value: '', label: 'Tm Durumlar' },
    { value: 'active', label: 'Aktif' },
    { value: 'inactive', label: 'Pasif' },
    { value: 'archived', label: 'Ariv' }
  ];

  const segmentOptions = [
    { value: '', label: 'Tm Segmentler' },
    { value: 'premium', label: 'Premium' },
    { value: 'standard', label: 'Standart' },
    { value: 'basic', label: 'Temel' }
  ];

  const hasActiveFilters = Object.values(filters).some(value => 
    value !== '' && value !== null && value !== undefined
  );

  return (
    <div className={`flex flex-wrap items-center gap-4 ${className}`}>
      <div className="flex items-center gap-2">
        <Filter className="h-4 w-4 text-gray-500" />
        <span className="text-sm font-medium text-gray-700">Filtreler:</span>
      </div>

      {/* Status Filter */}
      <div className="flex flex-col">
        <label className="text-xs text-gray-500 mb-1">Durum</label>
        <select
          value={filters.status || ''}
          onChange={(e) => updateFilter('status', e.target.value as 'active' | 'inactive' | undefined)}
          className="px-3 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-1 focus:ring-indigo-500 focus:border-indigo-500"
        >
          {statusOptions.map(option => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </select>
      </div>

      {/* Segment Filter */}
      <div className="flex flex-col">
        <label className="text-xs text-gray-500 mb-1">Segment</label>
        <select
          value={filters.segment || ''}
          onChange={(e) => updateFilter('segment', e.target.value)}
          className="px-3 py-2 border border-gray-300 rounded-md text-sm focus:outline-none focus:ring-1 focus:ring-indigo-500 focus:border-indigo-500"
        >
          {segmentOptions.map(option => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </select>
      </div>

      {/* Clear Filters */}
      {hasActiveFilters && (
        <button
          onClick={clearAllFilters}
          className="flex items-center gap-1 px-3 py-2 text-sm text-gray-600 hover:text-gray-800 hover:bg-gray-100 rounded-md transition-colors"
        >
          <X className="h-4 w-4" />
          Temizle
        </button>
      )}
    </div>
  );
};