import React from 'react';
import { Trash2, Archive, Mail, Download, Edit } from 'lucide-react';

interface PatientBulkActionsProps {
  selectedPatients: string[];
  onAction: (action: string, patientIds: string[]) => void;
  className?: string;
}

export const PatientBulkActions: React.FC<PatientBulkActionsProps> = ({
  selectedPatients,
  onAction,
  className = ""
}) => {
  if (selectedPatients.length === 0) {
    return null;
  }

  const actions = [
    {
      id: 'edit',
      label: 'Dzenle',
      icon: Edit,
      variant: 'primary' as const,
      disabled: selectedPatients.length > 1
    },
    {
      id: 'export',
      label: 'Da Aktar',
      icon: Download,
      variant: 'secondary' as const
    },
    {
      id: 'email',
      label: 'E-posta Gnder',
      icon: Mail,
      variant: 'secondary' as const
    },
    {
      id: 'archive',
      label: 'Arivle',
      icon: Archive,
      variant: 'warning' as const
    },
    {
      id: 'delete',
      label: 'Sil',
      icon: Trash2,
      variant: 'danger' as const
    }
  ];

  const getButtonClasses = (variant: string, disabled: boolean) => {
    const baseClasses = "flex items-center gap-2 px-3 py-2 text-sm font-medium rounded-md transition-colors";
    
    if (disabled) {
      return `${baseClasses} bg-gray-100 text-gray-400 cursor-not-allowed`;
    }

    switch (variant) {
      case 'primary':
        return `${baseClasses} bg-blue-600 text-white hover:bg-blue-700`;
      case 'secondary':
        return `${baseClasses} bg-gray-100 text-gray-700 hover:bg-gray-200`;
      case 'warning':
        return `${baseClasses} bg-yellow-100 text-yellow-800 hover:bg-yellow-200`;
      case 'danger':
        return `${baseClasses} bg-red-100 text-red-800 hover:bg-red-200`;
      default:
        return `${baseClasses} bg-gray-100 text-gray-700 hover:bg-gray-200`;
    }
  };

  return (
    <div className={`flex items-center gap-2 p-4 bg-blue-50 border border-blue-200 rounded-lg ${className}`}>
      <div className="flex-1">
        <p className="text-sm font-medium text-blue-900">
          {selectedPatients.length} hasta seildi
        </p>
      </div>
      
      <div className="flex items-center gap-2">
        {actions.map((action) => {
          const Icon = action.icon;
          return (
            <button
              key={action.id}
              onClick={() => !action.disabled && onAction(action.id, selectedPatients)}
              disabled={action.disabled}
              className={getButtonClasses(action.variant, action.disabled || false)}
              title={action.disabled ? 'Bu ilem iin tek hasta sein' : action.label}
            >
              <Icon className="h-4 w-4" />
              {action.label}
            </button>
          );
        })}
      </div>
    </div>
  );
};